import React, { useState } from 'react';
import './DataTable.css';
import data from '../Notes.json'; // Import JSON data
import PdfViewer from './PdfViewer'; // Import the PDF Viewer component

const DataTable = () => {
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredData, setFilteredData] = useState(data);
    const [selectedPdf, setSelectedPdf] = useState(null); // State for selected PDF

    const handleSearch = (e) => {
        const term = e.target.value;
        setSearchTerm(term);
        const results = data.filter((item) =>
            Object.values(item).some((val) =>
                val.toString().toLowerCase().includes(term.toLowerCase())
            )
        );
        setFilteredData(results);
    };

    const handlePdfClick = (link) => {
        setSelectedPdf(link); // Set the link directly from JSON
    };

    return (
        <div className="container">
            {!selectedPdf ? (
                <>
                    <input
                        type="text"
                        placeholder="Search..."
                        className="search-box"
                        value={searchTerm}
                        onChange={handleSearch}
                    />

                    <table className="data-table">
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Code</th>
                                <th>Course</th>
                                <th>Type</th>
                                <th>Link</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredData.map((item, index) => (
                                <tr key={index}>
                                    <td>{item.name}</td>
                                    <td>{item.code}</td>
                                    <td>{item.course}</td>
                                    <td>{item.type}</td>
                                    <td>
                                        <button
                                            className="view-btn"
                                            onClick={() => handlePdfClick(item.link)}
                                        >
                                            View PDF
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </>
            ) : (
                <PdfViewer fileUrl={selectedPdf} />
            )}
        </div>
    );
};

export default DataTable;
